@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  padding: 12px;
  font-family: 'Quicksand', sans-serif;
  overflow: scroll;
  background: white;
  width: 350px;
  height: 600px;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.credibility-bars {
  text-align: center;
  color: white;
}

/* - Red: hsl(0, 78%, 62%)
- Cyan: hsl(180, 62%, 55%)
- Orange: hsl(34, 97%, 64%)
- Blue: hsl(212, 86%, 64%)

### Neutral

- Very Dark Blue: hsl(234, 12%, 34%)
- Grayish Blue: hsl(229, 6%, 66%)
- Very Light Gray: hsl(0, 0%, 98%) */

:root {
  --red: hsl(0, 78%, 62%);
  --cyan: hsl(180, 62%, 55%);
 --orange: hsl(34, 97%, 64%);
  --blue: hsl(212, 86%, 64%);
  --darkblue: hsl(234, 12%, 34%);
  --grey: hsl(229, 6%, 66%);
  --lightgrey: hsl(0, 0%, 98%);
}

*, *:before, *:after {
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  max-width: 600px;
  margin: 50px auto;
  color: hsl(234, 12%, 34%);
  color: var(--darkblue);
}

header {
  text-align: center;
}

h1 span {
  display: block;
  font-weight: 200;
}

.boxes {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 600px;
  margin: 10px auto;
}

.box {
  border-radius: 8px;
  background-color: #fff;
  width: 330px;
  display: flex;
  flex-direction: column;
  height: auto;
  position: relative;
  padding: 10px;
  margin: 5px;
  box-shadow: 0 10px 20px rgba(0,0,0,0.06), 0 6px 6px rgba(0,0,0,0.1);
  overflow: hidden;
}

.box:before {
  content: '';
  height: 5px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: red;
}

.box.box-vert-center {
  transform: translateY(50%);
}

.box p {
  padding-top: 0.75rem;
  font-size: 0.85rem;
}

.box .svg {
  margin-left: auto;
  margin-top: auto;
}

.box:nth-of-type(1):before {
  background-color: hsl(180, 62%, 55%);
  background-color: var(--cyan);
}

.box:nth-of-type(2):before {
  background-color: hsl(0, 78%, 62%);
  background-color: var(--red);
}

.box:nth-of-type(3):before {
  background-color: hsl(212, 86%, 64%);
  background-color: var(--blue);
}

.box:nth-of-type(4):before {
  background-color: hsl(34, 97%, 64%);
  background-color: var(--orange);
}

@media (max-width: 850px) {
  .box.box-vert-center {
    transform: translateY(0);
  }
}

.showMore {
  display:block;
  font-weight:700;
  text-transform:uppercase;
  margin:20px;
  margin-bottom: 30px;
  color:#0077C8;
  text-decoration:none;
  cursor: pointer;
}

.textClamp{
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

